//mixins se utilizan para crear funciones que se pueden repetir las veces que deseamos, a continuacion vemos un ejemplo
//donde creamos un mediaquery para telefono, al ponerle @conent le estamos indicando que pondremos atributos independientes
//ne varios selectores ya que cada selector usara distintos atributos, en donde se colocan llevan @include nombremixin

@mixin telefono {
  @media (min-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: 768px) {
    @content;
  }
}
@mixin lap {
  @media (min-width: 1200px) {
    @content;
  }
}
@mixin grid($columnas, $espaciado) {
  display: grid;
  grid-template-columns: repeat($columnas, 5fr);
  gap: $espaciado;
}
