.galeria {
  padding: 5rem 0;
  h2 {
    padding: 3rem;
    text-align: center;
  }
}
.galeria-imagenes {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  @include grid(2, $espaciado);
  @include tablet {
    @include grid(4, $espaciado);
  }
  @include lap {
    @include grid(5, 2.5rem);
  }
  li {
    max-width: 200px;
  }
}
.overlay {
  position: fixed;
  background-color: $obscurot;
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}
.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: $blanco;
  display: grid;
  place-items: center;
  font-weight: $negra;
  font-size: 4rem;
  border-radius: 50%;

  &:hover {
    cursor: pointer;
    color: $blanco;
    background-color: $obscuro;
  }
}
