.presentaciones {
  background-color: $obscuro;
  padding: 3rem 0;
  h2 {
    text-align: center;
    color: $blanco;
    text-transform: capitalize;
  }
  .dia {
    text-align: center;
    color: $blanco;
    text-transform: capitalize;
  }
  .escenario {
    padding-top: 3rem;
    margin: 3rem 0;
  }

  .nombre-escenario {
    text-transform: capitalize;
    color: $obscuro;
    font-weight: $regular;
    text-align: center;
    &:after {
      content: "";
      width: 40rem;
      height: 40rem;
      display: block;
      margin: 3rem auto 2rem auto;
      background-size: 30rem;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }
}
.indio .nombre-escenario {
  &:after {
    background-image: url(../img/indio.png);
  }
}
.indio2 .nombre-escenario {
  &:after {
    background-image: url(../img/indio2.png);
  }
}
.atat .nombre-escenario {
  &:after {
    background-image: url(../img/atat.png);
  }
}
.doritos .nombre-escenario {
  &:after {
    background-image: url(../img/doritos.png);
  }
}
.intolerante .nombre-escenario {
  &:after {
    background-image: url(../img/icono_rock.png);
  }
}
.calendario {
  padding: 0;
  list-style: none;
  li {
    text-transform: uppercase;
    padding: 3rem;
    font-weight: $bold;
  }
}
.verde .calendario li:nth-child(odd) {
  background-color: darken($verde, 5);
  color: rgb(201, 200, 200);
}
.amarillo .calendario li:nth-child(odd) {
  background-color: darken($amarillo, 10);
}
.blanco .calendario li:nth-child(odd) {
  background-color: darken($blanco, 20);
}
.anaranjado .calendario li:nth-child(odd) {
  background-color: darken($anaranjado, 10);
}
.azul .calendario li:nth-child(odd) {
  background-color: darken($azul, 10);
}
