.descripcion{
    padding: 5rem;
    @include tablet{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    }
    .imagen{
        max-height: 50rem;
        width: 100%;
        height: 100%;
        padding: 3rem 0;
    }
    
}
.texto{
    display: flex;
    flex-direction: column;
    justify-content: center;
    .fecha{
        color: $azul;
    }

}