html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}
body.fijar-body {
  overflow: hidden;
}

p {
  color: #090A0B;
  font-weight: 500;
}

.contenedor {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-weight: 700;
}

h3 {
  font-weight: 500;
}

.verde {
  background-color: #005744;
}

.amarillo {
  background-color: #ddb85d;
}

.blanco {
  background-color: #fff;
}

.anaranjado {
  background-color: #fa1802;
}

.azul {
  background-color: #009DDC;
}

.obscuro {
  background-color: #090A0B;
}

.header {
  background-color: #fff;
  margin-bottom: 0rem;
}
.header .contenido_header {
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .header .contenido_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header img {
  width: 5rem;
  height: 5rem;
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header img {
    width: 10rem;
    height: 10rem;
    margin-bottom: 0;
    margin: 0;
  }
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #444;
}

.nav_bar_principal {
  display: grid;
  justify-content: center;
  margin: 1rem;
  border-bottom: 5px solid #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .nav_bar_principal {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
    justify-content: center;
    border-bottom: 5px solid #fff;
  }
}
.nav_bar_principal a {
  margin-bottom: 1rem;
  color: #090A0B;
  font-size: 2.2rem;
  font-weight: 700;
}
.nav_bar_principal a:hover {
  color: #009DDC;
  border-bottom: 5px solid #009DDC;
}

.video {
  position: relative;
  max-height: 100rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #0029e08e, #00a8e08e);
}
.video video {
  display: block;
  width: 100%;
  height: 100%;
}

.contenido_video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.contenido_video h1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  font-weight: 900;
  font-size: 5rem;
}
.contenido_video h1 span {
  background-color: #ff0000;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 900;
}

.descripcion {
  padding: 5rem;
}
@media (min-width: 768px) {
  .descripcion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}
.descripcion .imagen {
  max-height: 50rem;
  width: 100%;
  height: 100%;
  padding: 3rem 0;
}

.texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.texto .fecha {
  color: #009DDC;
}

.presentaciones {
  background-color: #090A0B;
  padding: 3rem 0;
}
.presentaciones h2 {
  text-align: center;
  color: #fff;
  text-transform: capitalize;
}
.presentaciones .dia {
  text-align: center;
  color: #fff;
  text-transform: capitalize;
}
.presentaciones .escenario {
  padding-top: 3rem;
  margin: 3rem 0;
}
.presentaciones .nombre-escenario {
  text-transform: capitalize;
  color: #090A0B;
  font-weight: 500;
  text-align: center;
}
.presentaciones .nombre-escenario:after {
  content: "";
  width: 40rem;
  height: 40rem;
  display: block;
  margin: 3rem auto 2rem auto;
  background-size: 30rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.indio .nombre-escenario:after {
  background-image: url(../img/indio.png);
}

.indio2 .nombre-escenario:after {
  background-image: url(../img/indio2.png);
}

.atat .nombre-escenario:after {
  background-image: url(../img/atat.png);
}

.doritos .nombre-escenario:after {
  background-image: url(../img/doritos.png);
}

.intolerante .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  padding: 0;
  list-style: none;
}
.calendario li {
  text-transform: uppercase;
  padding: 3rem;
  font-weight: 700;
}

.verde .calendario li:nth-child(odd) {
  background-color: #003e30;
  color: #c9c8c8;
}

.amarillo .calendario li:nth-child(odd) {
  background-color: #d4a633;
}

.blanco .calendario li:nth-child(odd) {
  background-color: #cccccc;
}

.anaranjado .calendario li:nth-child(odd) {
  background-color: #c71302;
}

.azul .calendario li:nth-child(odd) {
  background-color: #0079a9;
}

.galeria {
  padding: 5rem 0;
}
.galeria h2 {
  padding: 3rem;
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 5fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 5fr);
    gap: 5rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 5fr);
    gap: 2.5rem;
  }
}
.galeria-imagenes li {
  max-width: 200px;
}

.overlay {
  position: fixed;
  background-color: #090a0b70;
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: #fff;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 4rem;
  border-radius: 50%;
}
.btn-cerrar:hover {
  cursor: pointer;
  color: #fff;
  background-color: #090A0B;
}

.abonos {
  padding: 5rem;
}
.abonos h2 {
  text-align: center;
  color: #fff;
}
.abonos .contenedor-boletos {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 5fr);
  gap: 5rem;
}
.abonos .abdia {
  color: #fff;
  text-align: center;
  margin-bottom: 2rem;
  border-bottom: 3px solid #fff;
}
.abonos .abono {
  width: 100%;
  color: #fff;
  word-spacing: 1rem;
  padding: 1rem;
  text-align: center;
  list-style: none;
}
.abonos th {
  padding: 1rem;
  text-align: start;
}
.abonos td {
  padding: 1rem;
  text-align: start;
}
.abonos .boton {
  text-align: center;
  padding: 1rem;
  border-radius: 20rem;
}
.abonos .boton:hover {
  cursor: pointer;
  color: #009DDC;
}

.footer {
  background-color: #090A0B;
  padding: 5rem;
  text-align: center;
}
.footer p {
  color: #fff;
  text-transform: uppercase;
}