html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*::before,
*:after {
  box-sizing: inherit;
}
body {
  font-size: 16px;
  font-family: $fuente;

  &.fijar-body {
    overflow: hidden;
  }
}
p {
  color: $obscuro;
  font-weight: $regular;
}
.contenedor {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  max-height: 100%;
}
h1,
h2,
h3 {
  margin: 0;
}
h1 {
  font-size: 4rem;
  font-weight: $negra;
}
h2 {
  font-weight: $bold;
}
h3 {
  font-weight: $regular;
}
