.header {
  background-color: $blanco;
  margin-bottom: 0rem;

  //mixin generado en el archivo de mixin
  .contenido_header {
    padding: 1rem 0;
    @include tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  img {
    width: 5rem;
    height: 5rem;
    display: block;
    margin: 0 auto;
    margin-bottom: 1rem;
    @include tablet {
      width: 10rem;
      height: 10rem;
      margin-bottom: 0;
      margin: 0;
    }
  }

  &.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 0.3rem 0.4rem #444;
  }
}

.nav_bar_principal {
  display: grid;
  justify-content: center;
  margin: 1rem;
  border-bottom: 5px solid $blanco;
  text-align: center;
  @include tablet {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
    justify-content: center;
    border-bottom: 5px solid $blanco;
  }

  a {
    margin-bottom: 1rem;
    color: $obscuro;
    font-size: 2.2rem;
    font-weight: $bold;
    &:hover {
      color: $azul;
      border-bottom: 5px solid $azul;
    }
  }
}
