.abonos {
  padding: $espaciado;
  h2 {
    text-align: center;
    color: $blanco;
  }
  .contenedor-boletos {
    margin-top: $espaciado;
    @include grid(2, $espaciado);
  }
  .abdia {
    color: $blanco;
    text-align: center;
    margin-bottom: 2rem;
    border-bottom: 3px solid $blanco;
  }
  .abono {
    width: 100%;
    color: $blanco;
    word-spacing: 1rem;
    padding: 1rem;
    text-align: center;
    list-style: none;
  }
  th {
    padding: 1rem;
    text-align: start;
  }
  td {
    padding: 1rem;
    text-align: start;
  }
  .boton {
    text-align: center;
    padding: 1rem;
    border-radius: 20rem;
    &:hover {
      cursor: pointer;
      color: $azul;
    }
  }
}
