.video {
  position: relative;
  max-height: 100rem;
  overflow: hidden;
  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, $degradadoo, $degradadoc);
  }
  video {
    display: block;
    width: 100%;
    height: 100%;
  }
}
.contenido_video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
  h1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: $blanco;
    font-weight: $negra;
    font-size: 5rem;
    span {
      background-color: $rojo;
      padding: 1rem;
      font-size: 2rem;
      font-weight: $negra;
    }
  }
}
